<template>
  <div class="upload">
    <div class="upload__wrapper">
      <h2 class="upload__title">
        Enter Link
        <font-awesome-icon :icon="['fa', 'link']" />
      </h2>

      <form @submit.prevent="fetchTheme($event)">
        <input class="upload__input" name="themeColor" id="themeColor" type="input" />
        <div class="upload__controller">
          <button class="upload__btn">
            Search link
            <font-awesome-icon :icon="['fa', 'search']" />
          </button>
          <input class="upload__file" id="binary" type="file" @change="getBinaryColorTheme" />
        </div>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: "UploadSection",
  methods: {
    fetchTheme($event) {
      const urlStr = $event.target.elements[0].value;
      // Reveal spinner
      this.$store.state.loading = true;
      this.$store.dispatch("getColorTheme", urlStr);
    },
    getBinaryColorTheme() {
      const input = document.getElementById("binary");
      const reader = new FileReader();

      reader.readAsBinaryString(input.files[0]);
      reader.addEventListener("load", () => {
        let binColorSource = window.btoa(reader.result);
        // Reveal spinner
        this.$store.state.loading = true;
        this.$store.dispatch("getBinaryColorTheme", { binColorSource });
      });
    }
  }
};
</script>

<style lang="scss">
.upload {
  &__wrapper {
    text-align: center;
    padding: 0 1rem;
  }

  &__controller {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;

    @include mobile {
      flex-direction: column;
      align-items: stretch;
    }
  }
  &__title {
    font-size: $font-2xl;
    color: $black;
    text-align: center;
    letter-spacing: 2px;

    @include mobile {
      font-size: $font-lg;
    }
  }

  &__input {
    margin: 0 auto;
    display: block;
    border: 1px solid $second-color;
    color: darkgrey;
    outline: none;
    padding: 0.75rem 1rem;
    width: 100%;
    margin-bottom: 2rem;
    border-radius: 6px;
    color: $second-color;

    @include mobile {
      margin-bottom: 1rem;
    }
  }

  &__btn {
    display: inline-block;
    font-size: $font-common;
    border: none;
    outline: none;
    padding: 1rem 1.6rem;
    border-radius: 0.2rem;
    letter-spacing: 1px;
    font-weight: 300;
    background-color: #428871;
    font-weight: 300;
    color: $white;
    cursor: pointer;
    font-family: inherit;
    @include tablet {
      margin-bottom: 0rem;
      padding: 0.7rem 1.2rem;
      font-size: $font-sm;
    }
  }

  &__file {
    position: relative;
    padding: 1rem 0.5rem;

    display: block;
    font-family: inherit;
    outline: none;
    &::after {
      position: absolute;
      content: "Extract colors from a local file";
      cursor: pointer;
      border-radius: 3px;
      display: flex;
      align-items: center;
      justify-content: center;
      top: 0;
      bottom: 0;
      width: 100%;
      font-family: inherit;
      background-color: rgb(170, 3, 3);
      text-align: center;
      color: $white;
      z-index: 1;
    }

    @include mobile {
      padding: 0.7rem 0;
      font-size: $font-sm;
    }
  }
}
</style>
