<template>
  <div class="spinner">
    <canvas id="spinner-canvas"></canvas>
  </div>
</template>

<script>
export default {
  name: "SpinnerComponent",
  mounted() {
    this.spin();
  },
  methods: {
    spin() {
      let enlargeRadius = 1.5;
      let reference = 0;
      const canvas = document.getElementById("spinner-canvas");
      const ctx = canvas.getContext("2d");
      const canvasParent = canvas.parentElement;
      canvas.width = canvasParent.offsetWidth;
      canvas.height = canvasParent.offsetHeight;

      const dotXReference = canvasParent.offsetWidth;
      const dotYReference = canvasParent.offsetHeight;
      function draw() {
        ctx.clearRect(0, 0, dotXReference, dotYReference);

        ctx.fillStyle = "#b34d34";
        ctx.beginPath();
        for (var i = 0; i < 5; i++) {
          if (i == reference) {
            ctx.arc(
              canvas.offsetWidth / 2 - 120 + i * 80,
              60,
              10 * enlargeRadius,
              0,
              Math.PI * 2
            );
          } else {
            ctx.arc(
              canvas.offsetWidth / 2 - 120 + i * 80,
              60,
              10,
              0,
              Math.PI * 2
            );
          }
        }

        ctx.fill();
        if (reference > 3) {
          reference = 0;
        } else {
          reference++;
        }
      }
      setInterval(draw, 200);
    }
  }
};
</script>

<style lang="scss" scoped>
.spinner {
  width: 100%;
  margin: 0 auto;
  min-height: 55rem;

  @include tablet {
    min-height: 25rem;
  }

  @include mobile {
    min-height: 15rem;
  }
}
</style>