<template>
  <div class="landscape">
    <h2 class="landscape__title underline underline--center">Image preview</h2>
    <img v-if="imageLink && !imageBase64" class="landscape__img" :src="imageLink" alt />
    <img
      v-else-if="imageBase64"
      class="landscape__img"
      :src="'data: image/jpeg;base64,'+ imageBase64"
      alt
    />
  </div>
</template>

<script>
export default {
  name: "ImagePreview",
  computed: {
    imageLink() {
      return this.$store.state.imageLink;
    },
    imageBase64() {
      return this.$store.state.binaryColorPreview;
    }
  }
};
</script>

<style lang="scss">
.landscape {
  max-width: 50rem;
  margin: 0 auto;
  text-align: center;

  &__title {
    text-align: center;
  }

  &__img {
    height: 25rem;

    @include tablet {
      width: 100%;
    }
  }
}
</style>